<template>
  <div
    class="explain"
    v-lazy:background-image="explainUrl"
    :key="explainUrl"
    :style="{height}"
    @click.prevent.self="referer"
  >
    <div
      class="explain-bt ignore"
      :style="styleObject"
    >
      <van-button
        type="primary"
        @click="show = true"
        v-if="!show"
      >查看更多</van-button>
      <van-button
        type="primary"
        v-if="!show || role === 'p'"
        @click="referer"
      >查看课程</van-button>
    </div>
  </div>
</template>

<script>
import setHeight from "./mixins/set-height";
export default {
  mixins: [setHeight],
  data() {
    return {
      show: false,
      height: "100vh"
    };
  },
  computed: {
    explainUrl() {
      return this.show
        ? this.role === "s"
          ? "//keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/spread_student_2.jpg"
          : "//keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/spread2.jpg"
        : this.role === "s"
        ? "//keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/spread_student_1.jpg"
        : "//keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/spread1.jpg";
    },
    styleObject() {
      if (this.show) {
        return {
          bottom: "40px"
        };
      } else {
        return {
          top: "50vh"
        };
      }
    }
  },
  mounted() {
    this.setHeight();
  },
  methods: {
    referer() {
      this.jump("ClassroomReferer", this.$route.query, {}, true);
    },
    biClick() {
      if (!this.isS && this.show) {
        this.referer();
      }
    }
  }
};
</script>

<style lang="less" scoped>
.explain {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  width: 100vw;
  min-height: 100vh;
  background: #5272fb 0 0 no-repeat;
  background-size: 100% auto;
  &-bt.ignore {
    position: absolute;
    width: 100%;
    text-align: center;
  }
}
.van-button + .van-button {
  margin-left: 30px;
}
</style>
