export default {
    methods: {
        setHeight() {
            const self = this;
            this.$Lazyload.$on("loaded", function ({
                naturalHeight,
                naturalWidth
            }) {
                self.$store.commit("updateLoadingStatus", {
                    isLoading: false
                });
                if (self.show) {
                    const width =
                        document.body.clientWidth || document.documentElement.clientWidth;
                    let realHeight = (width * naturalHeight) / naturalWidth
                    if (self.role !== 's') {
                        realHeight = realHeight + 100
                    }
                    self.height = realHeight + "px";
                }
            });
        }
    },
    destroyed() {
        this.$Lazyload.$off("loaded");
    }
}
